<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default ({
  inject: ['reload'],  
  data () {
        return {
            columns: [
                {
                    title: 'ID',
                    key: 'id',
                    width: 80, 
                    align: 'center'
                },
                {
                    title: '名称',
                    key: 'name',
                    width: 320, 
                    align: 'center'
                },
                {
                    title: '分类',
                    key: 'tag_type',
                    width: 100, 
                    align: 'center'
                },
                {
                    title: '主题类型',
                    key: 'tag_state',
                    width: 100, 
                    align: 'center'
                },
                {
                    title: '添加时间',
                    key: 'addtime',
                    width: 160, 
                    align: 'center'
                },
                {
                    title: '关联标签',
                    key: 'taginfo1',
                    width: '', 
                    align: 'center',
                    render: (h,params) => {
                        if(params.row) {
                            const taginfo1 = params.row.taglisttaginfo1;
                        return h('div', {}, taginfo1)
                        }
                    }
                },
                {
                    title: '状态',
                    key: 'status',
                    width: 160, 
                    align: 'center',
                    render: (h, params) => {
                        // console.log(params.row)
                        if(params.row) {
                            const status = params.row.status;
                            const text = status == 1 ? '已发布' : status == 2 ? '隐藏' : '-';
                            const fontstyle = status == 2 ? 'bold' : 'normal'
                        return h('div', {
                            style: {
                                fontWeight:fontstyle,
                                cursor:'pointer'
                            },
                            on: {
                            click: () => {
                                this.warnModal = true
                                this.statustxt = status == 1 ? '隐藏' : '发布';
                                this.curMid = params.row.id;
                                this.curstatus = params.row.status;
                                
                            }
                        }
                        }, text)
                        }
                    }
                },
                {
                    title: '关联应用',
                    key: 'gcount',
                    width: 160, 
                    align: 'center',
                    render: (h,params) => {
                        const id = params.row.id;
                        const gcount = params.row.gcount;
                        return h('a', {
                            style: {
                                color: '#515a6e'
                            },
                            on: {
                                click: () => {
                                    this.$router.push({
                                        query: {
                                            tagid: id,
                                            page: '1'
                                        },
                                        path: '/data/tagApp'
                                    })
                                }
                            }
                        }, gcount)
                    }
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 200, 
                    align: 'center',
                    render: (h, params) => {
                        const m_url = params.row.m_url;
                        const pc_url = params.row.pc_url;
                        return h('div', [
                            h('Button',{
                                domProps: {
                                    innerHTML: "编辑"
                                },
                                props: {
                                    type: 'primary',
                                    size: 'small',
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.gotomodelEdit(params)
                                    }
                                }
                            }, 'View'),
                            h('Button', {
                                props: {
                                type: 'warning',
                                size: 'small'
                                },
                                style: {
                                margin: '0 4px 2px 0'
                                },
                                on: {
                                    click: () => {
                                        window.open(pc_url,'_blank')
                                    }
                                }
                            }, 'pc预览'),
                            h('Button', {
                                props: {
                                type: 'warning',
                                size: 'small'
                                },
                                style: {
                                margin: '0 4px 2px 0'
                                },
                                on: {
                                    click: () => {
                                        window.open(m_url,'_blank')
                                    }
                                }
                            }, 'm预览')
                        ])
                    }
                },
            ],
            data: [],
            formData: {
            },
            curMid: '',
            curstatus: '',
            isLoading: true,
            page: 1,
            per_page: 10,
            totalPages: 0,
            params: {},
            warnModal: false,
            modal_loading: false,
            statustxt: '',
            gametagModel: '',
            statusModel: '0',
        }
    },
    mounted () {
        const token = getToken(),
            name = this.formData.name,
            status = this.formData.status*1,
            params = {"token": token, "page": "1", "per_page": 10,"name":name, "status":status};

            this.params = params;
            this.getData(params);
    },
    methods: {
        getData(params) {
        api.appTaglist(params).then(res => {
                if(res.code === 0) {
                    this.data = res.data.taglist
                    this.totalPages = res.data.totlerows
                    this.isLoading = false 
                    //格式化时间
                    this.data.forEach(ele => {
                        ele.taglisttaginfo1 = ''
                        ele.addtime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
                        if (ele.tagInfo1.length > 0) {
                            ele.tagInfo1.forEach(ele2 => {
                                ele.taglisttaginfo1 += (ele2.name) + '  '
                            })
                        }
                    })
                } else {
                    this.$Notice.error({title: res.msg})
                }
            })
        },
        gotomodelEdit (params) {
        const id = params.row.id
        const type = params.row.type
        this.$router.replace({
            path: '/data/editTagapp',
            query: {"id": id,"type": type}
        })
        },
        
        changePage: function(page) {
            this.params.page = page;
            this.getData(this.params);
        },
        changePageNum: function(num) {
            this.params.per_page = num;
            this.getData(this.params);
        },
        adminGroupDel: function() {
        this.modal_loading = true
        const token = getToken(),
                id = this.curMid*1,
                status = this.curstatus*1,
                type = 'tag',
                params = {"token": token, "id": id, "status": status, "type": type};
        api.ajaxUpdate(params).then(res => {
            this.warnModal = false
            this.modal_loading = false
            if(res.code == 0) {
            this.$Notice.success({
                "desc": res.msg
            })
            this.reload();
            } else {
            this.$Notice.error({
                "desc": res.msg
            })
            }
        }).catch(err => {
            this.warnModal = false
            this.modal_loading = false
            this.$Notice.error({
            "desc": err
            })
        })
        },
        deminGroupDel: function() {
            this.warnModal = false
            this.modal_loading = false
        },
        setNewData: function() {
        this.params.name = this.gametagModel;
        this.params.type = this.statusModel;
        // console.log(this.params,this.params.type)
        this.getData(this.params);
        },
        setchData: function() {
            this.params.status = this.statusModel
            this.getData(this.params)
        }
    }
})
</script>

<template>
    <div>
        <h3><Icon type="md-barcode" size=24 />应用专题列表</h3>
        
        <div class="formBox">
            <div class="searchBar">
                <Button type="primary" to="/data/editTagapp">添加专题</Button>
            </div>

            <div class="idbox"><span>名称：</span><Input  v-model="gametagModel" clearable style="width: 200px" /></div>

            <Button type="primary" style="width:100px" @click="setNewData()">
                <Icon type="ios-search" size=16></Icon>
                搜索
            </Button>

            <Select v-model="statusModel" style="width:100px" @on-change="setchData">
                <Option value="0">状态</Option>
                <Option value="1">已发布</Option>
                <Option value="2">隐藏</Option>
                <Option value="3">审核中</Option>
                <Option value="4">已退回</Option>
            </Select>
        </div>

        <Table 
            border 
            ref="selection" 
            :columns="columns" 
            :data="data" 
            :loading=isLoading
            >
        </Table>

        <Modal v-model="warnModal" width="400" :styles="{top:'40%'}">
            <p slot="header" style="color:#333;text-align:center">
                <span>提示</span>
            </p>
            <div style="text-align:center">
                <p>确认要{{ statustxt }}吗</p>
            </div>
            <div slot="footer">
                <Button type="primary" size="small" long :loading="modal_loading" @click="adminGroupDel"  style="width: 170px;margin-right:10px">确认</Button>
                <Button size="small" long :loading="modal_loading" @click="deminGroupDel" style="width: 170px">取消</Button>
            </div>
        </Modal>

        <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page :total="totalPages*1" show-total show-elevator show-sizer @on-page-size-change="changePageNum" @on-change="changePage" />
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .formBox {
        height: 50px;
        margin-bottom: 20px;
        .searchBar{
            display: contents;
            padding-right: 20px;
        }
        .idbox {
            display:contents;
            span{
                margin-left:20px; 
            }
        }
        Button{
            margin-left: 20px;
            margin-right: 20px;
        }
        .ivu-modal{
            top: 200px !important;
        }
    }
</style>